.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: $grisOscuro;
  padding: 1rem 0 3rem 0;

  &.inicio {
    background-position: center;
    background-size: cover;

    @include tablet {
      min-height: 70rem;
    }

    .contenido-header {
      @include tablet {
        min-height: 70rem;
      }
    }
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  @include tablet {
    align-items: normal;
  }

  h1 {
    text-align: center;
    color: $blanco;
    font-weight: $bold;
    font-size: 3.8rem;
    max-width: 60rem;
    margin-top: 8rem;

    @include tablet {
      text-align: left;
      margin-top: 0;
    }
  }
}