.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;

  @include tablet {
    align-items: flex-end;
  }

  .dark-mode-btn {
    width: 3rem;
    margin: 3rem 0;
    filter: invert(100%);

    &:hover {
      cursor: pointer;
    }

    @include tablet {
      margin-top: 0;
    }
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;

  @include tablet {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.barra {
  .navegacion {
    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .3s ease-in-out, opacity 0.5s linear, height 3s linear;
  
    @include tablet {
      opacity: 1;
      visibility: visible;
    }
  }
} 

.navegacion {
  a {
    color: $blanco;
    font-size: 2.2rem;
    display: block;
    text-align: center;

    @include tablet {
      font-size: 1.8rem;
    }
  }

  @include tablet {
    display: flex;
    gap: 2rem;
    align-items: center;
  }
}

.mobile-menu {
  @include tablet {
    display: none;
  }

  img {
    width: 5rem;
  }

  cursor: pointer;
}

.barra {
  .mostrar {
    opacity: 1;
    visibility: visible;
    height: auto;
  }
}