//* MEDIA QUERIES
@mixin telefono {
    @media (width >= $telefono) {
        @content;
    }
}

@mixin tablet {
    @media (width >= $tablet) {
        @content;
    }
}
  
@mixin desktop {
    @media (width >= $desktop) {
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin boton($bgColor, $display) {
    background-color: $bgColor;
    color: $blanco;
    font-weight: $bold;
    padding: 1rem 3rem;
    text-align: center;
    margin-top: 2.5rem;
    display: $display;
    transition: background-color .3s ease-in-out;
    border: none;

    &:hover {
        background-color: darken($bgColor, 5);
        cursor: pointer;
    }
}