.contenedor-anuncios {
  @include tablet {
    @include grid(3, 2rem);
  }
}

.anuncio {
  background-color: lighten($gris, 5);
  border: 1px solid darken($gris, 5);
  margin-bottom: 3rem;

  &:last-of-type {
    margin-bottom: 0;
  }

  @include tablet {
    margin-bottom: 0;
  }

  .contenido-anuncio {
    padding: 2rem;
  }

  h3,
  p {
    margin: 0;
  }

  view-timeline-name: --image;
  view-timeline-axis: block;
  animation-timeline: --image;
  animation-name: show;
  animation-range: entry 25% cover 30%;
  animation-fill-mode: both;
}

.precio {
  color: $verde;
  font-weight: $bold;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
  gap: 3rem;
  align-items: center;
  justify-content: space-around;

  li {
    display: flex;
    gap: 2rem;
    font-weight: $bold;
    max-height: 4rem;
  }
}