.alerta {
  padding: .5rem;
  width: 50%;
  text-align: center;
  color: $blanco;
  font-weight: $bold;
  text-transform: uppercase;
  margin: 1rem auto;
  border-radius: .5rem;

  &.error {
    background-color: $rojo;
  }

  &.exito {
    background-color: $verde;
  }
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;

  thead {
    background-color: $verde;

    th {
      color: $blanco;
      padding: 2rem;
    }
  }

  .imagen-tabla {
    width: 17rem;
    margin: 0 auto;
  }
}

.imagen-small {
  width: 30rem;
  margin-left: 1rem;
}